
import { BrowserRouter, Routes, Route } from "react-router-dom";
import Layout from "./Layout/Layout";
import HomeComponent from '../Pages/Home';
import Registration from '../Pages/Registration';
import GuideServices from "../Pages/GuideServices";
import VisaConsultancy from "../Pages/VisaConsultancy";
import Transportation from "../Pages/Transportation";
import TravelInsurance from "../Pages/TravelInsurance";
import Activities from "../Pages/Activities";


function Routing() {
  return (
    <div className="App">
      <BrowserRouter>
        <Layout>
          <Routes>
            <Route path="/" element={<HomeComponent />} />
            <Route path="/Registration" element={<Registration />} />
            <Route path="/GuideServices" element={<GuideServices />} />
            <Route path="/VisaConsultancy" element={<VisaConsultancy />} />
            <Route path="/Transportation" element={<Transportation />} />
            <Route path="/TravelInsurance" element={<TravelInsurance />} />
            <Route path="/Activities" element={<Activities />} />


          </Routes>
        </Layout>
      </BrowserRouter>

    </div>
  );
}

export default Routing;